$header-height: 50px;

.header {
  height: $header-height;
  background-color: $white;
  border-bottom: 1px solid $light-grey-2;

  .wrapper {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    height: $header-height;
  }

  &__title {
    display: flex;
    align-items: center;

    &__logo {
      height: $header-height;
      width: $header-height;
      background: #999;
      margin-right: $gap-small;
    }

    h1 {
      margin: 0;
    }
  }

  &__cart {
    position: relative;
    width: $header-height;
    background: #999;

    &__link {
      display: block;
    }
  }
}
